<template>
<div>
        <link rel="stylesheet" href="static/css/newuser.css">
    <link rel="stylesheet" href="static/css/style.css">
    <div class="container user_box">
        <div class="panel panel-info">
            <div class="panel-heading">
                <h3 class="panel-title">注册帐号</h3>
            </div>
            <div class="panel-body">
                <div class="col-sm-12">
                    <a class="btn btn-link  pull-right" id="index" href="#/">返回登录页面</a>
                </div>
                <form  class="form form-horizontal col-sm-4 col-sm-offset-4" name="myForm" :action="HOST+'register.action'" method="post" ref="myForm">
                    <div class="form-group"  v-html="errormsg"></div>
                    <div class="form-group" :class="{'has-success':!errors.has('tel')&&newuser.tel!='','has-error':errors.has('tel')}">
                        <div class="col-sm-12" >
                            <input type="text" name="tel" v-model="newuser.tel" placeholder="请输入11位手机号" data-vv-as="手机号" class="form-control" v-validate="'required|tel'" @input="removecodeMSg"></div>
                        <div class="col-sm-11 col-sm-offset-1 text-alert text-danger">
                           <span v-show="errors.has('tel')">{{ errors.first('tel') }}</span>
                           <span v-html="codeerrormsg"></span>
                        </div>
                        
                    </div>
                    <div class="form-group" :class="{'has-success':!errors.has('code')&&newuser.code!='','has-error':errors.has('code')}">
                        <div class="col-sm-7">
                            <input type="text" name="code" data-vv-as="验证码" v-model="newuser.code" placeholder="请输入验证码" class="form-control" v-validate="'required|numeric|length:6'"
                                :disabled="errors.has('tel')||newuser.tel==''">
                        </div>
                        <div class="col-sm-5"><button type="button"  class="btn btn-primary btn-block" @click="loginCode" :disabled="errors.has('tel')||newuser.tel==''||codedis" ref="code" v-html="codeinner"></button></div>
                        <div class="col-sm-11 col-sm-offset-1 text-alert text-danger">
                            <span v-show="errors.has('code')">{{ errors.first('code')}}</span>
                        </div>
                    </div>
                    <div class="form-group" :class="{'has-success':!errors.has('userid')&&newuser.userid!='','has-error':errors.has('userid')}">
                        <div class="col-sm-12">
                            <input type="text" name="userid" data-vv-as="用户名" v-model="newuser.userid" placeholder="请输入用户名" class="form-control"  v-validate="'required|cnlength:6'"></div>
                        <div class="col-sm-11 col-sm-offset-1 text-alert text-danger">
                            <span v-show="errors.has('userid')">{{ errors.first('userid')}}</span>
                        </div>
                    </div>
                    <div class="form-group" :class="{'has-success':!errors.has('password')&&newuser.pass!='','has-error':errors.has('password')}">
                        <div class="col-sm-12">
                            <input type="password" name="password" data-vv-as="密码" v-model="newuser.pass" placeholder="请输入密码" class="form-control"  v-validate="'required|password'">
                            </div>
                        <div class="col-sm-11 col-sm-offset-1 text-alert text-danger">
                             <span v-show="errors.has('password')">{{ errors.first('password')}}</span>
                        </div>
                        <div class="col-sm-12" style="font-size:8px;">密码为字母、数字和符号两种及以上的组合，6-16个字符</div>
                    </div>
                    <div class="form-group" :class="{'has-success':!errors.has('repw')&&newuser.pass==newuser.password&&newuser.password!='','has-error':errors.has('repw')||newuser.pass!=newuser.password}">
                        <div class="col-sm-12">
                            <input type="password" name="repw" data-vv-as="确认密码" v-model="newuser.password" placeholder="再次输入密码" class="form-control"  v-validate="'required'"></div>
                        <div class="col-sm-11 col-sm-offset-1 text-alert text-danger">
                            <span v-show="errors.has('repw')">{{ errors.first('repw')}}</span>
                            <span v-show="newuser.pass!=newuser.password&&newuser.password!=''">两次密码不符</span>
                        </div>
                    </div>
                    <div class="form-group">
                        <button type="button" class="btn btn-info col-sm-5 col-sm-offset-3" @click="submits">注册</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</div>
</template>
<script>
export default {
    data:()=>{
        return {
            newuser:{
                tel:"",
                code:"",
                userid:"",
                pass:"",
                password:""
            },
            codedis:false,
            codeinner:"获取验证码",
            codeerrormsg:"",
            errormsg:""
        }
    },
    methods:{
        removecodeMSg(){var vm=this;vm.codeerrormsg=""},
        loginCode(){
            var vm=this;
            var tel= vm.newuser.tel;
           if(vm.codeerrormsg){
               return;
           }else{
                vm.$axios.post(vm.HOST+"sendCode.action",{tel:tel}).then(function(response){
                console.log(response.data)
                var msg=response.data.msg;
                const bool=(msg=="本号码已存在,"?true:false);
                msg=bool?msg+"请<a href='#/'>直接登录</a>":msg;
                vm.codeerrormsg=msg;
                vm.codedis=bool;
                if(!bool){
                    vm.codeDis();
                      vm.$layer.alert("验证码已发送，请注意查收");
                }
            }).catch(function(error){
                console.log(error);
                 vm.$layer.alert("网络错误,请重新获取验证码");
            })
           }
        },
        codeDis(){
           var vm=this;
           vm.codedis=true;
            var i=10;
            upText();
            let times=setInterval(upText,1000);
            function upText(){
                vm.codeinner=i+"秒后继续";
                i-=1;
                if(i<=-1){
                    clearInterval(times);
                    vm.codeinner="再次获取";
                    vm.codedis=false;
                }
            }
        },
        submits(){
            var vm=this;
            var newuser=vm.newuser;
            vm.$validator.validateAll().then(function(value){
                if(value&&newuser.pass==newuser.password){
                    vm.$refs.myForm.submit();
                }
            })
        }
    }
}
</script>




