<template>
    <div>
         <div class="col-sm-12">
            <div class="col-sm-2">
                <h5 ><span v-text="statestitle"></span>{{buttoninfor.title}}</h5>
            </div>
            <div class="col-sm-12">
                <form class="form form-horizontal col-sm-8" name="myform">
                  <!-- 特有输入栏 -->
                   <div v-if="stateId=='gongnengguanli'"  class="form-group">
                      <!-- 选择菜单级别 -->
                      <label for="" class="col-sm-2 control-label">类别</label>
                        <div>
                          <div class="col-sm-1">
                            <input type="radio" name="type" id="type1" value="1" v-model="addData.type"><label for="type1" class="control-label">目录</label> 
                          </div>
                          <div class="col-sm-1">
                            <input type="radio" name="type" id="type2" value="2" :checked="true" v-model="addData.type"><label for="type2"  class="control-label">菜单</label> 
                          </div>
                          <div class="col-sm-1">
                            <input type="radio" name="type" id="type3" value="3" v-model="addData.type"><label for="type3"  class="control-label">按钮</label> 
                          </div>
                        </div>
                   </div>
                   <!-- 公共输入栏 -->
                    <div class="form-group" >
                        <label class="control-label col-sm-2"><span v-text="statestitle"></span>名</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('name')&&addData.name!='','has-error':errors.has('name')}">
                            <input type="text" class="form-control" name="name" :placeholder="'请输入'+statestitle+'名,最多为20位'" v-model="addData.name" :data-vv-as="statestitle+'名'"
                             v-validate="validateAdd('name')">
                            </div>
                             <div class="text-danger">
                                 <span v-show="errors.has('name')">{{errors.first("name")}}</span>
                        </div>
                    </div>
                    <!-- 特有输入栏 -->
                    <div v-if="stateId=='gongnengguanli'">
                      <!--菜单url-->
                      <div class="form-group" v-if="addData.type==2">
                        <label class="control-label col-sm-2">菜单url</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('url')&&addData.url!='','has-error':errors.has('url')}">
                            <input type="text" name="url" class="form-control" placeholder="请输入菜单url,只能输入英文和数字" v-model="addData.url" 
                              data-vv-as="url"   v-validate="validateAdd('url')"></div>
                             <div  class="text-danger">
                            <span v-show="errors.has('url')">{{errors.first("url")}}</span>
                        </div>
                      </div>
                      <!-- 上级菜单 -->
                      <div class="form-group">
                        <label for="" class="col-sm-2 control-label">上级菜单</label>
                        <div class="col-sm-6">
                          <input type="text" class="form-control" value="一级菜单" v-model="addData.parent_id">
                        </div>
                      </div>
                       <div class="form-group" v-if="addData.type!=1">
                        <label for="" class="col-sm-2 control-label">授权标识</label>
                        <div class="col-sm-6">
                          <input type="text" class="form-control" placeholder="用多个逗号分隔；如user:list,user:delete" v-model="addData.perms">
                        </div>
                      </div>
                      <div v-if="addData.type!=3">
                        <div class="form-group">
                        <label for="" class="col-sm-2 control-label">排序号</label>
                        <div class="col-sm-6">
                          <input type="number" class="form-control" v-model="addData.order_num">
                        </div>
                      </div>
                      <div class="form-group">
                        <label for="" class="col-sm-2 control-label">图标</label>
                        <div class="col-sm-6">
                          <input type="text" class="form-control" placeholder="菜单图标" v-model="addData.icon">
                        </div>
                      </div>
                      </div>
                    </div>
                     <!-- 公共输入栏 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">详情描述</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('mydetail')&&addData.detail!='','has-error':errors.has('mydetail')}">
                            <textarea rows="6" class="form-control" name="mydetail" placeholder="请输入描述信息，最多不超过50个字符" v-model="addData.detail" data-vv-as="描述"
                            v-validate="'required|cnlength:50'"></textarea>
                            </div>
                         <div class="text-danger">
                          <span v-show="errors.has('mydetail')">{{errors.first("mydetail")}}</span>
                        </div>
                    </div>
                    <div class="form-group col-sm-12">
                        <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info" @click="addDatas">提交</button> 
                        <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="inforAddclick">返回</button> 
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data: () => {
    return {
      initData: {
        id: "",
        name: "",
        url: "",
        detail: "",
        type: "2",
        order_num: 0
      },
      addData: {},
      nameVer: "",
      urlVer: ""
    };
  },
  props: ["stateId", "statestitle", "buttoninfor"],
  watch: {
    "buttoninfor"(newvalue){
     this.loginbuttonInfor();
    }
  },
  mounted: function() {
    this.loginbuttonInfor();
    const validate = (value, args) =>
      new Promise(resolve => {
        if (value) {
          var data = {
            data: {
              data: value,
              name: args[0]
            },
            states: {
              id: this.stateId
            }
          };
          return resolve(
            this.$axios
              .post(this.HOST + "inspectData.action", data)
              .then(function(success) {
                if (success.data.msg != "") {
                  return {
                    valid: false,
                    data: {
                      message: success.data.msg
                    }
                  };
                } else {
                  return {
                    valid: true,
                    data: {
                      message: ""
                    }
                  };
                }
              })
              .catch(function(error) {
                console.log(error);
                console.log("网络错误");
                return {
                  valid: true,
                  data: {
                    message: ""
                  }
                };
              })
          );
        } else {
          return true;
        }
      });
    const val = {
      getMessage: function(name) {
        return "已存在";
      },
      validate: validate
    };
    this.$validator.extend("val", val);
  },
  filters: {},
  methods: {
    loginbuttonInfor() {
      var data;
      if (this.buttoninfor) {
        data = this.$api.ObjectCopy(this.buttoninfor);
        if (data.checkData) {
          if (data.checkData.length == 1)
            this.addData = this.cutStr("detail", "name", data.checkData[0]);
        }
      } else {
        this.addData = this.$api.ObjectCopy(this.initData);
      }
    },
    cutStr(name1, name2, Obj) {
      var obj = Obj;
      for (var key in obj) {
        if (key.indexOf(name1) > -1 && key != name1) {
          obj[name1] = obj[key];
          delete obj[key];
        }
        if (key.indexOf(name2) > -1 && key != name2) {
          obj[name2] = obj[key];
          delete obj[key];
        }
      }
      return obj;
    },
    validateAdd(name) {
      var Str = "required|cnlength:20";
      var Addstr = Str + "|val:" + name;
      if (this.buttoninfor.name == "add") {
        return Addstr;
      } else {
        return Str + "";
      }
    },
    inforAddclick() {
      this.addData = this.$api.ObjectCopy(this.initData);
      this.$api.emitApi(this, "", false);
    },
    //数据添加操作
    addDatas: function() {
      var vm = this;
      var stateId = vm.stateId;
      var data = vm.addData;
      var datas = {
        data: data,
        states: {
          id: stateId
        }
      };
      vm.$validator.validateAll().then(function(value) {
        if (value) {
          vm.$axios
            .post(vm.HOST + "distributor.action", datas)
            .then(function(success) {
              console.log(success);
              vm.$layer.alert('<div><i class="icon icon-success"></i>添加成功</div>');
              data = {};
            })
            .catch(function(error) {
              vm.$layer.alert('<div><i class="icon icon-error"></i>添加失败</div>');
            });
        }
      });
    }
  }
};
</script>





