<template>
  <div class="list-group">
    <a class="list-group-item list-group-item-heading list-item-head">模块管理</a>
    <ul>
      <li v-for="(menu,index) in menus" :key="index">
        <button @click="changeMenu(index,menu,$event)" v-if="menu.type === 0" :class="['list-group-item btn btn-link menu-list',{active:menu.show}]">
          <span>{{menu.title}}</span>
        </button>
        <ul :class="['ul-item',{block:menu.show,none:!menu.show}]" >
          <li v-for="list of menu.list" :key="list.href">
            <router-link class="ul-a" active-class="active" :to="'/main/'+list.href" v-text="list.title"></router-link>
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data: function() {
    return {
      menus: []
    };
  },
  created: function() {
    var vm = this;
    vm.loginmenu(vm);
  },
  methods: {
    loginmenu: function(vm) {
      vm.$axios
        .get("static/json/menu.json")
        .then(function(response) {
          vm.menus = response.data.menu;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    changeMenu(index, item,e) {
     var event=e.target.nextSibling.nextSibling;
      this.menus.forEach(i => {
        // 判断如果数据中的headerData[i]的show属性不等于当前数据的show属性那么headerData[i]等于false
        if (i.show !== this.menus[index].show) {
          i.show = false;
        }
      });
      // var length=this.menus[index].list.length;
      // var times=setTimeout(function loadtime(){
      //   // var fontsize=getComputedStyle(document.getElementsByTagName("body")[0],null).fontSize;
      //   var height=getComputedStyle(event.getElementsByTagName("a")[0],null).height;
      //   console.log(height)
      //   event.style.height=parseInt(event.style.height)+5+"px";
      //   if(length*parseInt(height)<=parseInt( event.style.height)){
      //     clearTimeout(times);
      //      event.style.height=length*parseInt(height)+"px";
      //   }else{
      //     setTimeout(loadtime,10);
      //   }
      // },10);
      item.show = !item.show;

    }
  }
};
</script>
<style scoped>
.active {
  z-index: 2;
  color: #fff;
  background-color: #337ab7!important;
  border-color: #f1f3f5;
}
.menu-list:hover,.menu-list.active{
  background: #bdbfc0!important;
}
ul {
  list-style-type: none;
  padding: 0;
}
.ul-item {
  display: none;
}
.block {
  display: block;
}
.none{
  display: none;
}
.ul-a{
  height: 3rem;
  line-height: 3rem;
  width: 100%;
  display: block;
  text-decoration: none;
  text-align: center;
  background: #ccc;
  color: #000;
}
.ul-a:hover{
  color: #fff!important;
  background: #3f688b!important;
}
</style>



