<template>
    <div>
        <div>
            <button type="button" class="btn btn-info" @click="inforAddClick">新增</button>
            <button type="button" class="btn btn-info" @click="updateData">修改</button>
            <button type="button" class="btn btn-info" @click="deleteData">删除</button>
            <button type="button" class="btn btn-info">审核</button>
            <button type="button" class="btn btn-info">冻结</button>
        </div>
    </div>
</template>

<script>
export default {
  data: () => {
    return {};
  },
  props: ["checkData", "stateId"],
  methods: {
    inforAddClick() {
      this.$api.emitApi(this, { name: "add", title: "添加" }, true);
    },
    updateData() {
      var vm = this;
      var checkData = vm.checkData;
      var len = checkData.length;
      if (len > 1) {
        vm.$layer.alert("修改信息条数只能为1");
      } else if (len <= 0) {
        vm.$layer.alert("请选择修改信息");
      } else {
        vm.$api.emitApi(vm, { name: "update", title: "修改" }, true);
      }
    },
    deleteData() {
      var vm = this;
      var checkData = vm.checkData;
      var len = checkData.length;
      if (len > 1) {
        vm.$layer.alert("一次只能删除一条数据");
      } else if (len <= 0) {
        vm.$layer.alert('<div class="layer divtext"><i class="icon icon-tips"></i>请选择需要删除的数据</div>');
      } else {
        var id=vm.$layer.confirm('<div class="layer divtext"><i class="icon icon-question"></i>是否删除</div>', () => {
            vm.$layer.close(id);
          var datas = {
            id: checkData[0].id,
            states: {
              id: vm.stateId
            }
          };
          vm.$axios
            .post(vm.HOST+"deleteData.action", datas)
            .then(Response => {
              if (Response.code == "0") {
                vm.$layer.alert("删除成功");
              } else {
                vm.$layer.alert("删除失败");
              }
            })
            .catch(error => {
              vm.$layer.alert("网络错误");
            });
        });
      }
    }
  }
};
</script>

<style>
</style>



