const api = {
  emitApi: function (vm, title, value) {
    vm.$emit("buttonClickCall", title, value);
    vm.$validator.errors.clear(); //清除表单错误
  },
  ObjectCopy: objectcopy

}

function objectcopy(obj) {
  var data;
  if (obj instanceof Object) {
    data = {};
  }
  if (obj instanceof Array) {
    data = [];
  }
  for (var key in obj) {
    if (typeof obj[key] == "object") {
      data[key] = objectcopy(obj[key]);
    } else {
      data[key] = obj[key];
    }
  }
  return data;
}
export default api;



